@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    /* color: #000; */
    font-family: Poppins,sans-serif;
    font-weight: 400;
}
.logo{
    list-style: none;
}
.navbar{
    display: flex; /*! justify-content: space-around; */ align-items: center; height: 75px; /*! background-color: #FFFFFF; */
    width: 92%;
    margin: 0 auto;
}
.navbar a li{
    padding: 0 15px;
}
.logo{
    font-size: 30px;
}
.nav-links{
    display: flex;
    /*! justify-content: flex-end; */
    list-style: none;
    width: 72%;
    /*! text-align: center; */
    justify-content: center;
}
.item{
    color: #555252;
    display: inline-block;
    font-size: 15px;
    font-weight: 500;
    letter-spacing: .8px;
    /*! line-height: 94px; */
    text-decoration: none;
    font-family: Poppins;
    font-weight: 500;

}
.mobile-menu-icon{
    display: none;
}
.close-nav{
    display: none;
}
.search-btn,.user-btn {
    color: #000;
    font-size: 23px;
    border: none;
    background: transparent;
    margin-left: 15px;
  }
  .compare-btn,.heart-btn,.cart-btn{
    color: #000;
    font-size: 25px;
    border: none;
    background: transparent;
    margin-left: 15px;
  }
@media screen and (max-width:780px){
    .nav-links{
        display: none;
    }
    .navbar a li {
        padding: 0 0px;
      }

    .nav-links-mobile{
        position: absolute;
        display: block;
        list-style: none;
        /*! background-color: #005469; */
        right: 0;
        top: 0px;
        transition: all 0.5s ease-out;
        width: 73%;
        text-align: left;
        background-color: red;
        height: 49vh;
    }
    .item{
        text-align: left;
        /*! padding: 32px; */
        width: 100%;
        transition: all 0.5s ease;
    }
    .signup{
        border: none;
    }
    .mobile-menu-icon{
        display: block;
        position: absolute;
        font-size: 30px;
        /* color: white;
        background-color: #00828c; */
        border: none;
        outline: none;
        top: 14px;
        right: 27px;
        background-color: transparent;
        /*! background-color: yellow; */
        /*! width: 27%; */
        /*! padding: 11px 0px; */
    }
    .logo {
        font-size: 30px;
        position: absolute;
        top: 16px;
        left: 4px;
    }
}

